import React, {Component} from 'react'

export default class About extends Component {
    render() {
        return (
            <div className='col-sm d-flex align-items-center border-1px-black ml-3 mr-3'>
                <p className='d-flex justify-content-center txtNew font_7'>Приятно осознавать,
                    что ты двигаешься к цели каждый день, маленькими шажками, единичными действиями
                    в правильном направлении!<br/><br/>
                    Нам важно не количество клиентов, а количество их улыбок! Каждая из которых
                    выражает приятное удивление.

                    <br/><br/>
                    Спасибо вам за то, что вы цените чистоту! Ждем ваши улыбки в наших центрах)</p>
            </div>
        )
    }
}



